"use client"; // mark this as a client component since you use hooks and state

import { useState } from "react";
import { useRouter } from "next/navigation";
import { useDispatch } from "react-redux";
import axios from "axios";
import { addUser } from "../utils/userSlice"; // adjust your path accordingly

const BASE_URL = "http://your-api-url.com"; // replace with your backend

export default function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const router = useRouter();
  const dispatch = useDispatch();

  const handleLogin = async (e) => {
    e.preventDefault();

    if (!email || !password) {
      alert("Please enter both email and password.");
      return;
    }

    try {
      const res = await axios.post(
        `${BASE_URL}/login`,
        { email, password },
        { withCredentials: true }
      );

      if (
        res.data === "Incorrect password" ||
        res.data.message === "User not found!"
      ) {
        alert("Invalid email or password");
      } else {
        dispatch(addUser(res.data));
        localStorage.setItem("user", JSON.stringify(res.data));
        router.push("/dashboard");
      }
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <section className="bg-gray-50 dark:bg-gray-900">
      {/* ... your form markup here, same as you wrote ... */}
      <form onSubmit={handleLogin} className="space-y-4 md:space-y-6">
        {/* input fields */}
        <input
          onChange={(e) => setEmail(e.target.value)}
          value={email}
          type="email"
          name="email"
          id="email"
          placeholder="name@company.com"
          required
          className="..." // your tailwind classes
        />
        <input
          onChange={(e) => setPassword(e.target.value)}
          value={password}
          type="password"
          name="password"
          id="password"
          placeholder="••••••••"
          required
          className="..."
        />
        <button type="submit" className="...">
          Sign in
        </button>
      </form>
      {/* your register links with router.push instead of navigate */}
      <p>
        Are you an Admin?{" "}
        <a
          href="#"
          onClick={(e) => {
            e.preventDefault();
            router.push("/signupadmin");
          }}
          className="..."
        >
          Register here
        </a>
      </p>
      <p>
        Don’t have an account yet?{" "}
        <a
          href="#"
          onClick={(e) => {
            e.preventDefault();
            router.push("/signupuser");
          }}
          className="..."
        >
          Sign up
        </a>
      </p>
    </section>
  );
}
